.formello_form_page_formello-settings #wpcontent {
  padding-left: 0;
}

.formello-dashboard-wrap {
  margin: 0;
  overflow: hidden;
}

.formello-dashboard-header {
  background: #fff;
  border-bottom: 1px solid #e2e4e7;
  padding: 40px 0 0;
  text-align: center;
}
.formello-dashboard-header h1 {
  font-size: 25px;
  font-weight: 600;
  padding-bottom: 0;
  margin-bottom: 20px;
}
.formello-dashboard-header .formello-dashboard-header-content {
  max-width: 700px;
  margin: 0 auto;
}

.formello-navigation {
  background: #fff;
  display: flex;
  justify-content: center;
}
.formello-navigation a {
  padding: 0.5rem 1rem 1rem;
  display: inline-flex;
  color: inherit;
  text-decoration: none;
}
.formello-navigation a.active {
  font-weight: 600;
  box-shadow: inset 0 -3px #007cba;
}

.formello-settings-area .components-placeholder.formello-settings-placeholder {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 750px;
  background: #fff;
  border: 1px solid #e2e4e7;
  box-shadow: none;
}
.formello-settings-area .formello-settings-header {
  background: #fff;
  padding: 10px;
  margin-bottom: 15px;
}
.formello-settings-area .formello-settings-header h1 {
  font-size: 17px;
  font-weight: 600;
  display: flex;
  align-items: center;
}
.formello-settings-area .formello-settings-header svg {
  width: 25px;
  height: 25px;
  margin: 0 10px;
}
.formello-settings-area .formello-settings-main {
  margin-left: auto;
  margin-right: auto;
  max-width: 750px;
  /* Only needed until WP 5.6 */
}
.formello-settings-area .formello-settings-main .formello-action-button {
  display: flex;
  align-items: center;
}
.formello-settings-area .formello-settings-main .formello-action-message {
  padding-left: 10px;
  opacity: 0;
  transition: opacity 200ms ease-in-out;
  color: green;
  pointer-events: none;
}
.formello-settings-area .formello-settings-main .formello-action-message.formello-action-message--show {
  opacity: 1;
  transition: opacity 200ms ease-in-out;
}
.formello-settings-area .formello-settings-main .formello-action-message.formello-action-message--error {
  color: red;
}
.formello-settings-area .formello-settings-main .formello-css-print-method .components-base-control__label {
  display: block;
}
.formello-settings-area .formello-settings-main .components-button .components-spinner {
  margin: 0;
}
.formello-settings-area .formello-settings-main .components-button.is-primary .components-spinner {
  background: #000;
}
.formello-settings-area .formello-settings-main .components-panel__body {
  margin: 25px 0;
  background: #fff;
  border: 1px solid #e2e4e7;
}
.formello-settings-area .formello-settings-main .components-panel__body .components-panel__body-toggle {
  border-bottom: 1px solid #d6e2ed;
}
.formello-settings-area .formello-settings-main .components-panel__body .formello-dashboard-panel-row-wrapper {
  padding-top: 10px;
}
.formello-settings-area .formello-settings-main .components-panel__body .formello-dashboard-panel-row-wrapper > *:last-child {
  margin-bottom: 0;
}
.formello-settings-area .formello-settings-main .components-panel__body .formello-dashboard-panel-row-wrapper label {
  font-weight: bold;
}
.formello-settings-area .formello-settings-main .components-panel__body .components-panel__row {
  padding: 0;
  margin: 0 0 25px;
  display: block;
}
.formello-settings-area .formello-settings-main .components-panel__body .components-panel__row .components-base-control .components-base-control__help {
  margin-bottom: 0;
}
.formello-settings-area .formello-settings-main .components-panel__body .components-panel__row .components-base-control .components-base-control__field {
  margin-bottom: 10px;
}
.formello-settings-area .formello-settings-main .components-panel__body .components-notice {
  margin: 0 0 10px;
}

.formello-dashboard-page #wpcontent {
  padding-left: 0;
}
.formello-dashboard-page .update-nag {
  margin-bottom: 20px;
  margin-left: 22px;
}

.formello-tablist .components-tab-panel__tabs {
  background-color: white;
}

.components-radio-control__input[type=radio]:checked::before {
  border: 0;
}

.components-radio-control__option {
  display: inline;
}

.components-tab-panel__tabs-item:focus:not(:disabled) {
  box-shadow: inset 0 0 0 1.5px transparent, inset 0 -4px 0 0 var(--wp-admin-theme-color);
  outline: none;
}

/* PAGES */
.formello-addons {
  display: flex;
  flex-wrap: wrap;
}

.addon {
  display: flex;
  flex-direction: column;
  width: 320px;
  border: 1px solid #ccc;
  margin: 0 20px 20px 0;
  background-color: #fff;
  padding: 10px;
}

.addon img {
  max-width: 100%;
  height: auto;
}

.addon footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.addon a {
  box-shadow: none;
}

.addon .type {
  float: right;
  text-transform: uppercase;
  font-weight: bold;
}

.column-id {
  text-align: left;
  width: 100px !important;
  overflow: hidden;
}

.badge {
  background-color: #337ab7;
  padding: 0 5px;
  margin-left: 10px;
  color: white;
  border-radius: 20%;
}

.column-actions {
  text-align: left;
  width: 150px !important;
  overflow: hidden;
}

.column-is_new {
  text-align: left;
  width: 70px !important;
  overflow: hidden;
}

.column-starred {
  text-align: left;
  width: 10px !important;
  overflow: hidden;
}

.badge {
  color: #337ab7;
  font-size: 12px;
  margin-top: 3px;
}

.star {
  color: #e27730;
  font-size: 12px;
}

/* TABLE SUBMISSION STYLE */
table.formello-bordered {
  font-size: 13px;
  border-collapse: collapse;
  border-spacing: 0;
  background: white;
  width: 100%;
  table-layout: fixed;
}

table.formello-bordered th, table.formello-bordered td {
  border: 1px solid #ddd;
  padding: 12px;
}

table.formello-bordered th {
  width: 160px;
  font-size: 14px;
  text-align: left;
}